.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


:root {
  --banner-height: 64px;
  color: white;
  background-image: url('../public/background_image_resized.png');
  background-size: cover;
  background-color: rgba(38, 38, 38, 1);
}

.icon{
  color: white;
}

.navBar{
  background-color: #120b1a;
  height: 5.5vh;
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: fixed;
  top: 0;
  z-index: 1000;
  box-shadow: 0px 2px 10px black;
}


